.process {
  width: 100%;
  margin-top: 50px;
}
.process__text {
  text-align: center;
  margin: 0 auto;
  max-width: 550px;
}
.process__text-slogan {
  color: #552768;
  font-family: Inter Tight, sans-serif;
  font-size: 18px;
}
.process__text-title {
  color: #552768;
  margin-top: 15px;
  font-family: Raleway, sans-serif;
  font-size: 38px;
  line-height: 48px;
}
.process__text-subtitle {
  color: #552768;
  font-family: Open Sans, sans-serif;
  font-size: 18px;
  line-height: 27px;
  margin-bottom: 10px;
}
.process__column {
  margin-top: 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.process__column-column-image:hover {
  transform: scale(1.1);
  transition-duration: 0.5s;
}

.process__column-column-title {
  width: 310px;
  text-align: center;
  font-size: 18px;
  line-height: 38px;
  font-family: "Fraunces", serif;
  font-weight: 500;
}
.process__column-column-subtitle {
  width: 310px;
  text-align: center;
  font-family: Open Sans, sans-serif;
  font-size: 18px;
  line-height: 27px;
  color: #62668a;
}

.process__image {
  display: flex;
  border: 1px solid #6b7db2;
  background-color: #6b7db2;
  border-radius: 54px;
  padding: 80px 80px 56px 80px;
  margin-top: 50px;
}
.process__image-text {
  max-width: 470px;
  width: 100%;
  margin-right: 30px;
}
.process__image-title {
  font-family: "Plus Jakarta Sans", sans-serif;
  font-size: 37px;
  line-height: 48px;
  color: #fff;
  margin: 0;
  padding: 0;
}
.process__image-subtitle {
  font-family: Open Sans, sans-serif;
  font-size: 18px;
  line-height: 27px;
  color: #fff;
  margin-bottom: 40px;
}
.process__image-button {
  color: #552769;
  width: 260px;
  text-align: center;
  background-color: #fff;
  border: 0 solid #fff;
  border-radius: 10px;
  padding: 20px 0;
  font-family: Plus Jakarta Sans, sans-serif;
  font-size: 18px;
}
.process__image-image {
  margin-top: 10px;
  margin-left: 20px;
  img {
    max-width: 500px;
    width: 100%;
  }
}
.line {
  margin-top: 50px;
}
.line-image {
  width: 100%;
}
@media screen and (max-width: 637px) {
  .process__text-title {
    font-size: 30px;
  }
  .process__column {
    justify-content: center;
  }
  .process__column-column-image {
    text-align: center;
  }
  .process__column-column-title {
    width: 100%;
  }
  .process__column-column-subtitle {
    width: 100%;
  }
}
@media screen and (max-width: 760px) {
  .process__image {
    display: flex;
    flex-direction: column;
  }
  .process__image-text {
    max-width: 100%;
    margin-right: 0;
  }
  .process__image-image {
    margin: 30px 0px 0px 0px;
  }
  .process__image {
    padding: 48px;
  }
  .process__image-title {
    font-size: 30px;
    margin-bottom: 20px;
    line-height: 40px;
  }
  .process__image-button {
    width: 220px;
  }
  .process__image-subtitle {
    font-size: 16px;
    line-height: 24px;
  }
}
@media screen and (max-width: 500px) {
  .process__image {
    padding: 20px;
  }
}
